.menu-btn .toggle {
  position: relative;
  margin: 0 auto 5px;
  width: 34px;
  height: 19px;
}
.menu-btn .toggle > span {
  display: block;
  width: 100%;
  height: 3px;
  position: absolute;
  left: 0;
  opacity: 1;
  background: #fff;
  transition: all 250ms ease;
}
.menu-btn .toggle > span:nth-child(1) {
  top: 0;
}
.menu-btn .toggle > span:nth-child(2),
.menu-btn .toggle > span:nth-child(3) {
  top: 8px;
}
.menu-btn .toggle > span:nth-child(4) {
  top: 16px;
}
.menu-btn > span {
  display: block;
  margin: 5px 0 0 0;
  color: #fff;
  font: 700 12px/1 'Roboto Condensed', arial, sans-serif;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.menu-btn.active .toggle > span:nth-child(1) {
  top: 9px;
  width: 0;
  left: 50%;
}
.menu-btn.active .toggle > span:nth-child(2) {
  transform: rotate(45deg);
}
.menu-btn.active .toggle > span:nth-child(3) {
  transform: rotate(-45deg);
}
.menu-btn.active .toggle > span:nth-child(4) {
  top: 9px;
  width: 0;
  left: 50%;
}
.menu-btn:hover {
  text-decoration: none;
}
