====================*/
/****************
*	1. mixin for showing desktop dropdown
*	2. mobile menu dropper element +
*	3. MAIN NAV (parent wrapper)
*	4. First Level Menu
*	5. Second Level Menu
*	6. Third Level Menu (should only show for mobile when necessary)
*******************/
span.main-nav__dropper {
  display: none;
  position: absolute;
  top: 0;
  right: -35px;
  width: 35px;
  height: 100%;
  cursor: pointer;
  z-index: 1050;
}
span.main-nav__dropper:before,
span.main-nav__dropper:after {
  content: "";
  display: block;
  position: absolute;
  top: 17px;
  left: 8px;
  width: 20px;
  height: 2px;
  background: #00aeef;
}
span.main-nav__dropper:after {
  transform: rotate(-90deg);
  transition: all 250ms ease;
}
