/*===========================
    DIRECTIONAL NAV - ARROWS
=============================*/
ul.flex-direction-nav li a {
  display: block;
  position: absolute;
  top: 50%;
  width: 50px;
  height: 50px;
  transform: translateY(-50%);
  background: #00aeef url('/wp-content/themes/groot/img/arrows/chevron-left-white.svg') center center no-repeat;
  overflow: hidden;
  white-space: nowrap;
  text-indent: 101%;
  z-index: 501;
}
ul.flex-direction-nav li a.flex-prev {
  left: 0;
}
ul.flex-direction-nav li a.flex-next {
  right: 0;
  background-image: url('/wp-content/themes/groot/img/arrows/chevron-right-white.svg');
}
ul.flex-direction-nav li a:hover {
  background-color: #0089bc;
}
/*===========================
    PAGING - DOTS
============================*/
ol.flex-control-paging {
  width: 100%;
  position: absolute;
  bottom: 10px;
  left: 0;
  text-align: center;
  z-index: 5;
}
ol.flex-control-paging li {
  display: inline-block;
}
ol.flex-control-paging li a {
  position: relative;
  display: block;
  width: 30px;
  height: 35px;
  overflow: hidden;
  white-space: nowrap;
  text-indent: 101%;
}
ol.flex-control-paging li a:before {
  content: "";
  display: block;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #ccc;
  transition: all 250ms ease;
}
ol.flex-control-paging li a.flex-active:before,
ol.flex-control-paging li a:hover:before {
  background: #00aeef;
}
/*===========================
    PAGING - THUMBNAILS
===========================*/
ol.flex-control-thumbs {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  margin-left: -5px;
  margin-right: -5px;
}
ol.flex-control-thumbs li {
  position: relative;
  padding-left: 5px;
  padding-right: 5px;
  flex-basis: 20%;
  max-width: 20%;
}
ol.flex-control-thumbs li img {
  display: block;
  margin: 10px 0 0 0;
  width: 100%;
  opacity: 0.5;
  transition: all 250ms ease;
  cursor: pointer;
}
ol.flex-control-thumbs li img:hover,
ol.flex-control-thumbs li img.flex-active {
  opacity: 1;
}
